import { RendererCanvas, Vector3, normalize, crossProduct, distanceEuclidean, reflect, dotProduct } from '../../../lib'

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#000';
        div.style.width = '1000px';
        div.style.height = '600px';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new Renderer(div);

        let btn = document.createElement('div');
        btn.style.paddingTop = '5px';
        btn.style.paddingBottom = '5px';
        div.appendChild(btn);
        btn.textContent = 'Click twice in different locations to build a wall';

        const sl2 = getSlider('bounces', 2, 200);
        div.appendChild(sl2.div);
        sl2.slider.oninput = () => {
            this.renderer.updateIter(+sl2.slider.value)
        };

        const cBox = getCheckBox('Ray Direction');
        div.appendChild(cBox.div);
        cBox.checkBox.checked = false;
        cBox.checkBox.onchange = () => {
            // console.log(cBox.checkBox.checked)
            if (cBox.checkBox.checked === true) {
                this.renderer.isRayDirection = true;
            } else {
                this.renderer.isRayDirection = false;
            }
        }

        const cBoxLine = getCheckBox('Line');
        div.appendChild(cBoxLine.div);
        cBoxLine.checkBox.checked = true;
        cBoxLine.checkBox.onchange = () => {
            // console.log(cBoxLine.checkBox.checked)
            if (cBoxLine.checkBox.checked === true) {
                this.renderer.isLineRenderer = true;
            } else {
                this.renderer.isLineRenderer = false;
            }
            this.renderer.updateIter(this.renderer.iter);
        }

    }
    public destroy() {
        this.renderer.destroy();
        try {
            while (this.divHost && this.divHost.lastElementChild) {
                const lastChild = this.divHost.lastElementChild as Node;
                this.divHost.removeChild(lastChild);
            }
        } catch (error) {
            console.error('Error removing child elements:', error);
        }
        
    }
}

export class Renderer extends RendererCanvas {

    walls: Wall[] = [];
    sun: Sun;
    t: number = 0.0;
    iter = 10;
    isRayDirection = false;
    isLineRenderer = true;
    private p0: Vector3 | undefined = undefined;
    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';


        this.sun = new Sun(new Vector3(300, 100, 0));
        this.walls.push(new Wall(50, 200, 400, 500))
        this.walls.push(new Wall(100, 500, 500, 600))
        this.walls.push(new Wall(500, 100, 400, 400))
        this.walls.push(new Wall(50, 100, 600, 50))
        this.walls.push(new Wall(500, 600, 600, 50))
        this.walls.push(new Wall(50, 100, 50, 300))
        this.walls.push(new Wall(50, 300, 100, 500))

        this.start();
    }
    public mouseClick(x: number, y: number): void {
        if (this.p0) {
            this.walls.push(new Wall(this.p0.x, this.p0.y, x, y));
            this.p0 = undefined;
            this.updateIter(this.iter);
            return;
        }

        if (this.p0 === undefined) {
            this.p0 = new Vector3(x, y);
            return;
        }
    }
    public mouseMove(x: number, y: number): void {
        if (this.isRayDirection) {
            this.sun.updateDirection(x, y);
            this.isStatic = false;
            this.loop(this.ctx);
        }
    }
    public updateIter(iter: number) {
        this.iter = iter;
        this.isStatic = false;
        this.loop(this.ctx);
    }
    public render(ctx: CanvasRenderingContext2D): void {
        this.t += 0.01;
        this.update(ctx);

        ctx.globalCompositeOperation = 'source-over';
        ctx.strokeStyle = '#ffffff';

        this.walls.forEach((w) => { w.draw(ctx); });
        this.sun.draw(ctx, this.t);

        this.isStatic = true;
    }
    public update(ctx: CanvasRenderingContext2D) {

        const sun = this.sun.getSunLine();


        const pts: any = computeInter(sun, this.walls, this.iter)

        ctx.lineWidth = 0.5
        for (let i = 0; i < pts.length; ++i) {
            const p = pts[i];
            ctx.beginPath();
            ctx.arc(p.x, p.y, 5, 0, Math.PI * 2.);
            ctx.closePath();
            ctx.stroke();
        }

        ctx.globalCompositeOperation = 'multiply';

        if (this.isLineRenderer) {
            ctx.lineWidth = 10
            if (pts.length > 0) {
                ctx.strokeStyle = `rgba(255, 0, 0, ${1})`;
                ctx.beginPath();
                ctx.moveTo(this.sun.v.x, this.sun.v.y)
                ctx.lineTo(pts[0].x, pts[0].y)
                ctx.stroke();
                for (let i = 0; i < pts.length - 1; i++) {
                    const o = 1.0 - i / pts.length;
                    // console.log(o)
                    ctx.strokeStyle = `rgba(255, 0, 0, ${o})`;
                    ctx.beginPath();
                    ctx.moveTo(pts[i].x, pts[i].y)
                    ctx.lineTo(pts[i + 1].x, pts[i + 1].y)
                    ctx.stroke();
                }
            }
        } else {
            if (pts.length > 0) {
                const vs: any = []
                vs.push(...getVecsBetweenVecsByDis(this.sun.v, pts[0], 20));
                for (let i = 0; i < pts.length - 1; i++) {
                    vs.push(...getVecsBetweenVecsByDis(pts[i], pts[1+i], 20));
                }
                for (let i = 0; i < vs.length; ++i) {
                    const p = vs[i];
                    const o = 1.0 - i / vs.length;
                    ctx.fillStyle = `rgba(255, 0, 0, ${o})`;
                    ctx.beginPath();
                    ctx.arc(p.x, p.y, 5, 0, Math.PI * 2.);
                    ctx.closePath();
                    ctx.fill();
                }
            }
    
        }
 


    }
}
const computeInter = (ray: any, walls: any, iter = 1000) => {

    let pts = []

    for (let j = 0; j < iter; ++j) {
        let theDis = 10000
        let theP;
        let wallNext;

        for (let i = 0; i < walls.length; ++i) {
            const wall = walls[i];
            const p = checkIntersection(ray.p0.x, ray.p0.y, ray.p1.x, ray.p1.y, wall.p0.x, wall.p0.y, wall.p1.x, wall.p1.y);
            if (p !== undefined) {

                const dis = distanceEuclidean(ray.p0.x, ray.p0.y, 0, p.x, p.y, 0);
                if (dis > 10 && dis < theDis) {

                    theP = p;
                    wallNext = wall;
                    theDis = dis;
                }
            }
        }

        if (theP !== undefined) {
            const rayVec = new Vector3(ray.p1.x - ray.p0.x, ray.p1.y - ray.p0.y, 0)
            // console.log(rayVec, wallNext.nVec)
            const rayBound = reflect(rayVec, wallNext.nVec)

            ray = {
                p0: theP,
                p1: {
                    x: theP.x + rayBound.x,
                    y: theP.y + rayBound.y
                }
            }
            // console.log(j , theP.x, rayBound.x);
            pts.push(theP)

        }

        // console.log(j);

    }
    // console.log(pts);
    return pts

}

const getVecsBetweenVecsByDis = (v0: Vector3, v1: Vector3, dis: number) => {
    const x = v1.x - v0.x;
    const y = v1.y - v0.y;
    const z = v1.z - v0.z;
    const nv = new Vector3(x, y, z);
    const num = nv.length() / dis
    nv.normalize();
    
    const vs = [];
    for(let i = 0 ; i < num; ++i) {
        const v = new Vector3(v0.x + nv.x * dis * i, v0.y + nv.y * dis * i, v0.z + nv.z * dis * i);
        vs.push(v);
    }
    return vs;
}



class Sun {
    v: Vector3;
    target = new Vector3(0, 0, 0);
    rayVec = new Vector3(0, 0, 0);;
    constructor(v: Vector3) {
        this.v = v;
        this.updateDirection(300, 300);
    }
    draw(ctx: CanvasRenderingContext2D, t: number) {
        ctx.lineWidth = 0.1;
        ctx.beginPath();
        ctx.moveTo(this.v.x, this.v.y);
        ctx.lineTo(this.target.x, this.target.y);
        ctx.stroke();

        ctx.lineWidth = 0.5;
        ctx.beginPath();
        ctx.moveTo(this.v.x, this.v.y);
        ctx.lineTo(this.v.x + this.rayVec.x * 50, this.v.y + this.rayVec.y * 50);
        ctx.stroke();

        ctx.fillStyle = '#FDB813'
        ctx.beginPath();
        ctx.arc(this.v.x, this.v.y, Math.sin(20 * t) + 12, 0, Math.PI * 2);
        ctx.fill();
    }
    updateDirection(x: number, y: number) {
        this.target.x = x;
        this.target.y = y;

        this.rayVec.x = this.v.x + this.target.x;
        this.rayVec.y = this.v.y + this.target.y;

        this.rayVec.x = this.target.x - this.v.x;
        this.rayVec.y = this.target.y - this.v.y;

        // this.rayVec = normalize(this.rayVec);
    }
    getSunLine() {
        return {
            p0: this.v,
            p1: new Vector3(this.v.x + this.rayVec.x * 1000, this.v.y + this.rayVec.y * 1000)
        }
    }
}

class Wall {
    p0: Vector3;
    p1: Vector3;
    nVec: Vector3;
    constructor(x0: number , y0: number , x1: number , y1: number) {
        this.p0 = new Vector3(x0, y0);
        this.p1 = new Vector3(x1, y1);
        this.computeNormal();
    }
    draw(ctx: CanvasRenderingContext2D) {
        ctx.lineWidth = 5;
        ctx.strokeStyle = '#ffffff';
        ctx.beginPath();
        ctx.moveTo(this.p0.x, this.p0.y);
        ctx.lineTo(this.p1.x, this.p1.y);
        ctx.stroke();
    }
    computeNormal() {
        const dir = new Vector3(this.p1.x - this.p0.x, this.p1.y - this.p0.y, 0)
        this.nVec = crossProduct(dir, new Vector3(0, 0, 1));
        this.nVec = normalize(this.nVec);
    }
}

export const pointOnSegment2D = (px: number, py: number, x0: number, y0: number, x1: number, y1: number) => {
    if (x0 !== x1) {
        if (x0 <= px && px <= x1) { return true; }
        if (x0 >= px && px >= x1) { return true; }
    } else {
        if (y0 <= py && py <= y1) { return true; }
        if (y0 >= py && py >= y1) { return true; }
    }
    return false;
}

export const checkIntersection = (x1: number, y1: number, x2: number, y2: number, x3: number, y3: number, x4: number, y4: number) => {
    const denom = ((y4 - y3) * (x2 - x1)) - ((x4 - x3) * (y2 - y1));
    const numeA = ((x4 - x3) * (y1 - y3)) - ((y4 - y3) * (x1 - x3));
    const numeB = ((x2 - x1) * (y1 - y3)) - ((y2 - y1) * (x1 - x3));

    if (denom === 0) {
        if (numeA === 0 && numeB === 0) {
            return undefined // COLINEAR;
        }
        return undefined // PARALLEL;
    }

    const uA = numeA / denom;
    const uB = numeB / denom;

    if (uA >= 0 && uA <= 1 && uB >= 0 && uB <= 1) {
        return ({
            x: x1 + (uA * (x2 - x1)),
            y: y1 + (uA * (y2 - y1)),
            z: 0
        });
    }

    return undefined;
}

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);

    return {
        div, slider
    }
};

const getCheckBox = (title: string) => {
    const div = document.createElement('div');
    div.style.display = 'flex';

    const checkBox = document.createElement('input');
    checkBox.type = 'checkbox';
    checkBox.defaultChecked = false;
    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(checkBox);
    div.appendChild(text);

    return {
        div, checkBox
    }
};